

<template>
  <div class="bookHistory">
    <HomeHeader :myBook="myBook" :isGoHome="isGoHome"> {{ goback }}</HomeHeader>
    <van-tabs v-model="currentIndex">
      <van-tab
        title="我的书架"
        to="/bookrack"
        title-style="margin:10px 0;border-right: 1px solid #ccc;"
      ></van-tab>
      <van-tab title="阅读记录" to="/bookHistory"></van-tab>
    </van-tabs>
    <BookrackItem
      v-if="readBooks"
      :books="readBooks"
      :isShowCtrl="isShowCtrl"
      @arr-trans="handler"
    />
  </div>
</template>

<script>
import HomeHeader from "@/components/HomeHeader.vue";
import BookrackItem from "@/components/BookrackItem.vue";

export default {
  data() {
    return {
      goback: "⮜ 返回",
      myBook: "最近阅读",
      currentIndex: 1,
      isGoHome: true,
      readBooks: [],
      isShowCtrl: false,
    };
  },
  components: {
    HomeHeader,
    BookrackItem,
  },
  created() {
    this.readBooks = JSON.parse(window.localStorage.getItem("readBooks")) || [];
  },
  methods: {
    //自定义事件传上来的arr
    handler(e) {
      this.arr = e;
    },
  },
};
</script>

