<template>
  <div>
    <van-swipe-cell v-for="(book, index) in books" :key="book._id">
      <van-card
        @click="readBook(book._id)"
        :desc="'阅读到第' + (book.currentOrder || 1) + '章'"
        :title="book.title"
        class="goods-card"
        :disabled="true"
        :thumb="decodeURIComponent(book.cover.slice(7) || book.cover)"
      >
        <template #num v-if="isShowCtrl">
          <input @change="handler(index)" type="checkbox" /> </template
      ></van-card>
    </van-swipe-cell>
  </div>
</template>

<script>
export default {
  props: ["books", "isShowCtrl"],
  data() {
    return {
      arr: [],
    };
  },
  created() {
    this.arr = this.books.map(() => {
      return false;
    });
  },
  methods: {
    handler(i) {
      this.arr[i] = !this.arr[i];
      this.$emit("arr-trans", this.arr);
      //   console.log(i, this.arr);
    },
    readBook(id) {
      if (this.isShowCtrl) {
        return;
      }
      console.log(1);
      this.axios.get("http://novel.kele8.cn/book-info/" + id).then((result) => {
        this.$store.commit("updateBookDetailData", result.data);
      });
      this.axios
        .get("http://novel.kele8.cn/book-sources?view=summary&book=" + id)
        .then((result) => {
          this.axios
            .get("http://novel.kele8.cn/book-chapters/" + result.data[0]._id)
            .then((res) => {
              this.$router.push("/book/" + id);
              this.$store.commit("updateChapter", res.data);
            });
        });
    },
  },
};
</script>

<style lang="less">
.goods-card {
  margin: 0;
  background-color: white;
  .van-card__thumb {
    width: 65px;
  }
  .van-card__thumb img {
    border-radius: 0;
  }
  .van-card__bottom {
    .van-card__num {
      input {
        position: absolute;
        top: 0;
        right: 0;
        width: 18px;
        height: 18px;
      }
    }
  }
}
</style>